import type { CSSObject } from '@emotion/react';
import type { HTMLAttributes } from 'react';
import React from 'react';

import { useDesignSystemTheme } from '../../design-system';
import type { DangerousGeneralProps } from '../../design-system/types';
import { addDebugOutlineIfEnabled } from '../../design-system/utils/debug';

export interface PreviewCardProps extends DangerousGeneralProps, Omit<HTMLAttributes<HTMLDivElement>, 'title'> {
  icon?: React.ReactNode;
  title?: React.ReactNode;
  subtitle?: React.ReactNode;
  titleActions?: React.ReactNode;
  startActions?: React.ReactNode;
  endActions?: React.ReactNode;
  image?: React.ReactNode;
  size?: 'default' | 'large';
  onClick?: React.MouseEventHandler<HTMLDivElement>;
}

export const PreviewCard = ({
  icon,
  title,
  subtitle,
  titleActions,
  children,
  startActions,
  endActions,
  image,
  onClick,
  size = 'default',
  dangerouslyAppendEmotionCSS,
  ...props
}: PreviewCardProps) => {
  const styles = usePreviewCardStyles({ onClick, size });
  const tabIndex = onClick ? 0 : undefined;
  const role = onClick ? 'button' : undefined;
  const showFooter = startActions || endActions;

  return (
    <div
      {...addDebugOutlineIfEnabled()}
      css={[styles['container'], dangerouslyAppendEmotionCSS]}
      tabIndex={tabIndex}
      onClick={onClick}
      role={role}
      {...props}
    >
      {image && <div>{image}</div>}
      <div css={styles['header']}>
        {icon && <div>{icon}</div>}
        <div css={styles['titleWrapper']}>
          {title && <div css={styles['title']}>{title}</div>}
          {subtitle && <div css={styles['subTitle']}>{subtitle}</div>}
        </div>
        {titleActions && <div>{titleActions}</div>}
      </div>
      {children && <div css={styles['childrenWrapper']}>{children}</div>}
      {showFooter && (
        <div css={styles['footer']}>
          <div css={styles['action']}>{startActions}</div>
          <div css={styles['action']}>{endActions}</div>
        </div>
      )}
    </div>
  );
};

const usePreviewCardStyles = ({
  onClick,
  size,
}: Pick<PreviewCardProps, 'onClick' | 'size'>): Record<string, CSSObject> => {
  const { theme } = useDesignSystemTheme();
  const isInteractive = onClick !== undefined;

  return {
    container: {
      borderRadius: theme.borders.borderRadiusLg,
      border: `1px solid ${theme.colors.border}`,
      padding: size === 'large' ? theme.spacing.lg : theme.spacing.md,
      color: theme.colors.textSecondary,
      display: 'flex',
      flexDirection: 'column',
      justifyContent: 'space-between',
      gap: size === 'large' ? theme.spacing.md : theme.spacing.sm,
      cursor: isInteractive ? 'pointer' : 'default',
      ...(isInteractive && {
        '&:hover, &:focus-within': {
          boxShadow: theme.general.shadowLow,
        },
        '&:active': {
          background: theme.colors.actionTertiaryBackgroundPress,
          borderColor: theme.colors.actionDefaultBorderHover,
          boxShadow: theme.general.shadowLow,
        },
        '&:focus': {
          outlineColor: theme.colors.actionPrimaryBackgroundDefault,
          outlineWidth: 2,
          outlineOffset: -2,
          outlineStyle: 'solid',
          boxShadow: theme.general.shadowLow,
          borderColor: theme.colors.actionDefaultBorderHover,
        },
        '&:active:not(:focus):not(:focus-within)': {
          background: 'transparent',
          borderColor: theme.colors.border,
        },
      }),
    },
    header: {
      display: 'flex',
      alignItems: 'center',
      gap: theme.spacing.sm,
    },
    title: {
      fontWeight: theme.typography.typographyBoldFontWeight,
      color: theme.colors.textPrimary,
      lineHeight: theme.typography.lineHeightSm,
    },
    subTitle: {
      lineHeight: theme.typography.lineHeightSm,
    },
    titleWrapper: {
      flexGrow: 1,
      overflow: 'hidden',
    },
    childrenWrapper: {
      flexGrow: 1,
    },
    footer: {
      display: 'flex',
      justifyContent: 'space-between',
      alignItems: 'center',
      flexWrap: 'wrap',
    },
    action: {
      overflow: 'hidden',
    },
  };
};
